<template>
  <div class="search" :style="searchStyle">
    <img class="search-icon" :src="icon" alt="" srcset="" />
    <input class="search-hint" placeholder="输入搜索内容" />
  </div>
</template>

<!-- :style="hintStyle" -->
<script>
export default {
  name: "SearchComponents",
  props: {
    bgColor: {
      default: "#ffffff",
      type: String,
    },
    hintColor: {
      default: "#999999",
      type: String,
    },
    icon: {
      default: require("@img/search.svg"),
      type: String,
    },
  },
  computed: {
    searchStyle() {
      return {
        backgroundColor: this.bgColor,
      };
    },
    hintStyle() {
      return {
        color: this.hintColor,
      };
    },
  },
};
</script>


