<template>
  <div class="home" @scroll="onScrollChange" ref="home">
    <navigation-bar
      :isDefault="false"
      :isShowBack="false"
      :navBarStyle="navBarStyle"
    >
      <template v-slot:nav-left>
        <img :src="navBarCurrentSlot.leftIcon" />
      </template>

      <template v-slot:nav-center>
        <search
          :bgColor="navBarCurrentSlot.search.bgColor"
          :hintColor="navBarCurrentSlot.search.hintColor"
          :icon="navBarCurrentSlot.search.icon"
        ></search>
      </template>

      <template v-slot:nav-right>
        <img :src="navBarCurrentSlot.rightIcon" />
      </template>
    </navigation-bar>

    <div class="home-content">
      <!-- 轮播图 -->
      <my-swiper
        :swiperImgs="swiperData.map((item) => item.icon)"
        :height="swiperHeight"
      ></my-swiper>

      <!-- 活动组件 520模块-->
      <activity>
        <div class="activity-520">
          <img
            v-for="(item, index) in activityDate"
            :key="index"
            :src="item"
            alt=""
          />
        </div>
      </activity>

      <!-- 功能组件 -->
      <mode-options />

      <!-- 限时秒杀组件 -->
      <seconds :dataSource="secondsDate" />

      <!-- 活动组件 拼购模块-->
      <activity>
        <div class="activity-ping-gou-jie">
          <img src="@img/haoHuoQiang.gif" alt="" />
        </div>
      </activity>

      <!-- 商品列表 -->
      <goods :layoutType="layoutType" :isScroll="false"></goods>
    </div>
  </div>
</template>

<!-- :layoutType="'0'" :isScroll="false" -->

<script>
import MySwiper from "@c/swiper/MySwiper.vue";
import Activity from "@c/currency/Activity.vue";
import ModeOptions from "@c/currency/ModeOptions.vue";
import Seconds from "@c/seconds/Seconds.vue";
import Goods from "@c/goods/Goods.vue";
import NavigationBar from "@c/currency/NavigationBar.vue";
import Search from "@c/currency/Search.vue";
export default {
  name: "HomeComponents",
  components: {
    MySwiper,
    Activity,
    ModeOptions,
    Seconds,
    Goods,
    NavigationBar,
    Search,
  },
  data() {
    return {
      swiperData: [
        {
          id: "1",
          icon: require("@img/swiper-1.jpg"),
        },
        {
          id: "2",
          icon: require("@img/swiper-2.jpg"),
        },
        {
          id: "3",
          icon: require("@img/swiper-3.jpg"),
        },
        {
          id: "4",
          icon: require("@img/swiper-4.jpg"),
        },
        {
          id: "5",
          icon: require("@img/swiper-5.jpg"),
        },
        {
          id: "6",
          icon: require("@img/swiper-6.jpg"),
        },
        {
          id: "7",
          icon: require("@img/swiper-7.jpg"),
        },
        {
          id: "8",
          icon: require("@img/swiper-8.jpg"),
        },
      ],
      swiperHeight: "184px", //window.isIphoneX ? '228px' : '184px',
      activityDate: [
        require("@img/520-1.gif"),
        require("@img/520-2.gif"),
        require("@img/520-3.gif"),
      ],
      secondsDate: [
        {
          id: "1",
          icon: require("@img/jingDongMiaoSha-1.jpg"),
          oldPrice: "169",
          price: "119",
        },
        {
          id: "2",
          icon: require("@img/jingDongMiaoSha-2.jpg"),
          oldPrice: "1900",
          price: "1191",
        },
        {
          id: "3",
          icon: require("@img/jingDongMiaoSha-3.jpg"),
          oldPrice: "289",
          price: "199",
        },
        {
          id: "4",
          icon: require("@img/jingDongMiaoSha-4.jpg"),
          oldPrice: "108",
          price: "79",
        },
        {
          id: "5",
          icon: require("@img/jingDongMiaoSha-5.jpg"),
          oldPrice: "1950",
          price: "588",
        },
        {
          id: "6",
          icon: require("@img/jingDongMiaoSha-6.jpg"),
          oldPrice: "316",
          price: "99.90",
        },
        {
          id: "7",
          icon: require("@img/jingDongMiaoSha-7.jpg"),
          oldPrice: "278",
          price: "239",
        },
        {
          id: "8",
          icon: require("@img/jingDongMiaoSha-8.jpg"),
          oldPrice: "2199",
          price: "1499",
        },
        {
          id: "9",
          icon: require("@img/jingDongMiaoSha-9.jpg"),
          oldPrice: "4299",
          price: "2069",
        },
        {
          id: "10",
          icon: require("@img/jingDongMiaoSha-10.jpg"),
          oldPrice: "245",
          price: "138.50",
        },
      ],
      //navbar背景样式
      navBarStyle: {
        position: "absolute ",
        backgroundColor: "rgba(0, 0, 0,0)",
      },
      //两个状态的具体样式
      navBarSlotData: {
        normal: {
          leftIcon: require("@img/more-white.svg"),
          rightIcon: require("@img/message-white.svg"),
          search: {
            bgColor: "#ffffff",
            hintColor: "#999999",
            icon: require("@img/search.svg"),
          },
        },
        highlight: {
          leftIcon: require("@img/more.svg"),
          rightIcon: require("@img/message.svg"),
          search: {
            bgColor: "#d7d7d7",
            hintColor: "#ffffff",
            icon: require("@img/search-white.svg"),
          },
        },
      },
      //组件的当前样式
      navBarCurrentSlot: {
        leftIcon: require("@img/more-white.svg"),
        rightIcon: require("@img/message-white.svg"),
      },
      scrollTopValue: -1,
      ANCHOR_SCROLL_TOP: 160,
      //商品布局样式
      layoutType: "2",
    };
  },
  created() {
    this.navBarCurrentSlot = this.navBarSlotData.normal;
    // this.initData();
  },
  activated() {
    this.$refs.home.scrollTop = this.scrollTopValue;
  },
  methods: {
    //获取数据
    initData() {
      this.$http
        .all([
          this.$http.get("/swiper"),
          this.$http.get("/activity-520"),
          this.$http.get("/seconds"),
        ])
        .then(
          this.$http.spread((swiperData, activityData, secondsDate) => {
            this.swiperData = swiperData.list;
            // console.log(this.swiperData);
            this.activityDate = activityData.list;
            // console.log(this.activityDate);
            this.secondsDate = secondsDate.list;
            // console.log(this.secondsDate);
          })
        )
        .catch((err) => {
          console.log(err);
        });
    },
    //顶部滚动变化
    onScrollChange($e) {
      //滚动距离
      this.scrollTopValue = $e.target.scrollTop;
      //背景透明度
      let opacity = this.scrollTopValue / this.ANCHOR_SCROLL_TOP;
      if (opacity >= 1) {
        this.navBarCurrentSlot = this.navBarSlotData.highlight;
      } else {
        this.navBarCurrentSlot = this.navBarSlotData.normal;
      }

      this.navBarStyle.backgroundColor = "rgba(255, 255, 255, " + opacity + ")";
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@css/style.scss";
.home {
  width: 100%;
  height: 100%;
  overflow: hidden;
  overflow-y: auto;
  &-content {
    height: 100%;
    background-color: $bgColor;
    .activity {
      font-size: 0;
      &-520 {
        background-color: $bgColor;
        margin-top: px2rem(-8);
        border-top-left-radius: px2rem(8);
        border-top-right-radius: px2rem(8);
        img {
          display: inline-block;
          width: 33.33%;
        }
      }
      .activity-ping-gou-jie {
        background-color: white;
        img {
          width: 100%;
        }
      }
    }
  }
}
</style>
